#skills{
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
}

.skillsTitle{
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;

}
.skillDesc{
    font-weight: 300;
    font-size: 0.80rem;
    max-width: 60rem;
    padding: 0 2rem;

}
.skillBars{
    margin: 1.5rem;
    width: 100vw;
    max-width: 80%;
    text-align: left;

}
.skillBar{
    display: flex;
    margin: 1rem;
    padding: 1.5rem 2rem;
    background: rgb(50,50,50);
    border-radius: 0.5rem;
}

.skillBarImg{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin-right: 2rem;
    padding: 27px 0px;

}
.skillBarText{
    line-height: 9px;
    margin: 14px 0px;
}
.skillBarText>p{
    font-size: 0.75rem;
    font-weight: 200;


}
@media screen and (max-width:720px) {
    .skillBarText>p{
            font-size: 2vw;

    }
    
}

@media screen and (max-width:480px) {
    .skillBarText>p{
            font-size: 3vw;
            
    }
    .skillBarImg{
        height: 2.25rem;
        width: 2.25rem;

    }
    .skillBarText>h2{
        font-size: 5vw;

    }
    
}

